import { Component } from "react";
import styled from "styled-components";
import { ApolloConsumer } from "react-apollo";
import { getNotesHashtags } from "./helpers";
import gql from "graphql-tag";
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "reactstrap";

export default class extends Component {
  constructor(props) {
    super(props);
    this.state = { value: "", hashtags: [] };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this.setState({ value: this.props.firstTrade.notes });
  }

  handleChange(client) {
    return event => {
      // event.preventDefault();
      this.setState({ value: event.target.value }, this.handleSubmit(client));
    };
  }

  handleSubmit(client) {
    // return event => {
    // event.preventDefault();
    // alert("A name was submitted: " + this.state.value);
    // let splitted = this.state.value.split(" ");
    // for (let i = 0; i < splitted.length; i++) {
    //   if (splitted[i].includes("#")) {
    //     this.state.hashtags.push(splitted[i]);
    //   }
    //   if (i == splitted.length - 1) {
    console.log("HASHTAGS", this.state.hashtags);
    getNotesHashtags(this.state.value, this.state.hashtags).then(async res => {
      let notes = res[0];
      // let hashtags = res[1];
      let time = this.props.firstTrade.timestamp;
      let reso = client.mutate({
        mutation: gql`
          mutation addNotes($time: String!, $notes: String!) {
            addNotes(time: $time, notes: $notes)
          }
        `,
        variables: { time, notes }
      });

      let r = await reso;
      console.log(r);
    });
  }
  render() {
    return (
      <ApolloConsumer>
        {client => (
          <FullRow onSubmit={this.handleSubmit(client)}>
            <HashtagDiv>
              <AddHashtag />
            </HashtagDiv>

            <NotesInput
              type="text"
              value={this.state.value}
              onChange={this.handleChange(client)}
            ></NotesInput>
          </FullRow>
        )}
      </ApolloConsumer>
    );
  }
}

class AddHashtag extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    // let styles = {
    //   background: "none !important",
    //   backgroundColor: "none"
    // };
    return (
      <ApolloConsumer>
        {client => (
          <Dropdown
            direction="right"
            isOpen={this.state.btnDropright}
            toggle={() => {
              this.setState({ btnDropright: !this.state.btnDropright });
            }}
          >
            <DropdownToggle>+</DropdownToggle>
            <DropdownMenu>
              <DropdownItem>Another Action</DropdownItem>
            </DropdownMenu>
          </Dropdown>
        )}
      </ApolloConsumer>
    );
  }
}

const HashtagDiv = styled.div`
  background: #212528;
  margin: 20px 8px;
  text-align: center;
  vertical-align: middle;
  color: white;
  padding: 10px;
  padding-top: 8px;
`;

const FullRow = styled.form`
  display: flex;
  flex-direction: row;
`;

const NotesSubmit = styled.input`
  margin: 9px;
  padding: 15px;
  background: #212528;
  border: none;
  color: white;
`;

const NotesInput = styled.input`
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  background: #f8f8ff;
  border: none;
`;
